import { t } from 'i18next';
import { useCallback, useEffect, useMemo, useState, type FC } from 'react';

import {
  ProjectChamberType,
  SpaceTypeEnum,
  type User,
} from '@/apollo/generated/types';
import { Avatar } from '@/components/Avatar/Avatar';
import { InlineAvatar } from '@/components/Avatar/Inline/Inline';
import { TSpan } from '@/components/Typography';
import { Spacer } from '@/layout';
import { ConnectionDetailsModal } from '@/screens/Chamber/components/modals/ConnectionDetailsModal/ConnectionDetailsModal';
import { ConnectionInviteModal } from '@/screens/Chamber/components/modals/ConnectionInviteModal/ConnectionInviteModal';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';

import { Icon } from '@/components/Icon';
import { useLaunchDarkly } from '@/hooks/launchDarkly';
import { ChamberVisibilityInviteModal } from '@/screens/Chamber/components/modals/ChamberVisibilityInvite';
import { ConnectionDetailModalTabEnum } from '@/screens/Chamber/components/modals/ConnectionDetailsModal/types';
import { useConnectionDetailsHelper } from '@/screens/Chamber/components/modals/ConnectionDetailsModal/useConnectionDetailsHelper';
import { useEvents } from '@/features/events/hooks';
import NoumDashboardMetricsModal from '@/screens/Chamber/components/modals/NoumDashboardMetricsModal';
import {
  NoumDashboardMetricsModalTabEnum,
  NoumDashboradTypeEnum,
} from '@/screens/Chamber/components/modals/NoumDashboardMetricsModal/types';
import { useGetNoumByLinkContext } from '@/screens/Chamber/components/RightPanel/NoumByLinkProvider';
import { getFullName } from '@/utils/fullName';
import { cleanList } from '@/utils/list';
import { UserUtil } from '@/utils/user';
import { NoumMembersSkeleton } from './NoumMembersSkeleton';
import { NoumsWrapper } from './NoumsWrapper';
import {
  AvatarBadge,
  AvatarWrapper,
  CardWrapper,
  CardWrapperButton,
  CardWrapperToolTipButton,
  MembersContainer,
  MembersWrapper,
  OwnerWrapper,
  ToolTipHead,
  TopContainer,
} from './styles';

export const NoumMembers: FC = () => {
  const {
    connectedMembers,
    connectedMembersCount,
    followersCount: spaceFollowersCount = 0,
    isMasterNoum,
    space,
    isOwner,
    isFollowing,
    isConnected,
    loading,
    refetchConnectedMembers,
    refetchFollowersCount,
    noumType,
  } = useNoumContext();

  const { eventMeta, loading: eventsLoading } = useEvents({
    chamberId: space?._id || '',
    preventGetEvents: false,
  });

  const [defaultTab, setDefaultTab] = useState<string | undefined>();
  const [dashboardType, setDashboardType] = useState<
    NoumDashboradTypeEnum | undefined
  >();
  const [invitedNoumId, setInvitedNoumId] = useState<string | undefined>();
  const [isOpenInviteModal, setOpenInviteModal] = useState<boolean>(false);
  const [isQuickOpenInviteModal, setQuickOpenInviteModal] = useState(false);

  const {
    flags: { noumDashboardMetrics },
  } = useLaunchDarkly();

  const isBoxFilled = isOwner && !isMasterNoum;

  const isClickableConnections =
    isOwner || isConnected || (isMasterNoum && connectedMembersCount > 0);

  const handleClickConnections = useCallback(() => {
    if (isClickableConnections) {
      if (isMasterNoum || !isOwner || !noumDashboardMetrics)
        setDefaultTab(ConnectionDetailModalTabEnum.Connections);
      else if (isOwner) {
        setDashboardType(NoumDashboradTypeEnum.connections);
        setDefaultTab(NoumDashboardMetricsModalTabEnum.Statistics);
      } else setDefaultTab(ConnectionDetailModalTabEnum.Connections);
    }
  }, [isClickableConnections, isMasterNoum, isOwner, noumDashboardMetrics]);

  const { noumLinkData, loadingLinked, getLinkData } =
    useGetNoumByLinkContext();

  const { totalCount, loading: connectionDetailsLoading } =
    useConnectionDetailsHelper(
      '',
      ConnectionDetailModalTabEnum.ProjectSpaces,
      undefined,
      isMasterNoum ? space?.uid?._id : null,
    );

  const connectedProjectsCount = isMasterNoum ? totalCount : 0;

  const {
    totalCount: userNoumsCount,
    loading: loadingUserNoums,
    currentData: userNoums,
  } = useConnectionDetailsHelper(
    '',
    ConnectionDetailModalTabEnum.OwnedNoums,
    undefined,
    isMasterNoum ? space?.uid?._id : null,
  );

  useEffect(() => {
    getLinkData(space?._id || '');
    refetchConnectedMembers();
    if (refetchFollowersCount && space?._id) refetchFollowersCount();
  }, [getLinkData, refetchConnectedMembers, refetchFollowersCount, space?._id]);

  const linkedNoums = useMemo(
    () => noumLinkData?.getNoumLinkByNoumId?.link?.linkedNoums || [],
    [noumLinkData?.getNoumLinkByNoumId?.link?.linkedNoums],
  );

  const linkedNoumsCount = useMemo(
    () => noumLinkData?.getNoumLinkByNoumId?.link?.linkedNoumsCount || 0,
    [noumLinkData?.getNoumLinkByNoumId?.link?.linkedNoumsCount],
  );

  const isLinked = useMemo(
    () => noumLinkData?.getNoumLinkByNoumId?.link || false,
    [noumLinkData],
  );

  const followersCount = useMemo(
    () => (isLinked ? isLinked?.followersCount : spaceFollowersCount),
    [isLinked, spaceFollowersCount],
  );

  const showLinkedNoums = useMemo(() => linkedNoums.slice(0, 3), [linkedNoums]);

  const showFollowers = useMemo(
    () =>
      space?.type !== SpaceTypeEnum.Home &&
      space?.projectType !== ProjectChamberType.Secret &&
      noumType !== SpaceTypeEnum.RiseApplication,
    [noumType, space?.projectType, space?.type],
  );

  const showEvents = !isMasterNoum && isOwner;

  useEffect(() => {
    getLinkData(space?._id || '');
    /* this needs to be called only when spaceFollowersCount is changed */
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [spaceFollowersCount]);

  const profilePictures = useMemo(
    () =>
      connectedMembers
        .slice(0, 3)
        .map((member) => UserUtil.getProfilePicture(member as User) ?? ''),
    [connectedMembers],
  );

  const moreCounts = useMemo(
    () => connectedMembersCount - profilePictures.length,
    [connectedMembersCount, profilePictures],
  );

  if (
    (loading && !connectedMembers.length) ||
    (loadingLinked && !noumLinkData?.getNoumLinkByNoumId) ||
    (loadingUserNoums && !userNoums.length) ||
    eventsLoading
  ) {
    return <NoumMembersSkeleton />;
  }

  return (
    <MembersContainer>
      {!isMasterNoum && (
        <TSpan font="footnote-bold" colorToken="--text-body-neutral-default">
          {t('noumena.chamber.owner')}
        </TSpan>
      )}
      <TopContainer>
        {!isMasterNoum && (
          <OwnerWrapper data-testid="owner_wrapper">
            <AvatarWrapper>
              <Avatar url={space?.uid?.profile?.profilePicture} size="L" />
            </AvatarWrapper>
            <CardWrapper>
              <TSpan
                font="body-m-bold"
                colorToken="--text-card-neutral-highlighted"
              >
                {getFullName(
                  space?.uid?.firstName,
                  space?.uid?.middleName,
                  space?.uid?.lastName,
                )}
              </TSpan>
              <TSpan
                font="footnote"
                colorToken="--text-card-neutral-default"
                singleLine
                title={space?.uid?.title || ''}
              >
                {space?.uid?.title}
              </TSpan>
            </CardWrapper>
          </OwnerWrapper>
        )}
        <MembersWrapper data-testid="chamberMembers" isBoxFilled={isBoxFilled}>
          {!isBoxFilled && (
            <>
              {connectedMembersCount > 0 ? (
                <AvatarWrapper>
                  <InlineAvatar urls={profilePictures} size="L" borderedImage />
                  {moreCounts > 0 && (
                    <AvatarBadge
                      colorToken="--text-badge-brand-primary-default"
                      font="badge-count"
                    >{`+${moreCounts}`}</AvatarBadge>
                  )}
                  {profilePictures.length > 1 && <Spacer width={16} />}
                </AvatarWrapper>
              ) : (
                <AvatarWrapper data-testid="empty_avatar">
                  <Avatar size="M" />
                </AvatarWrapper>
              )}
            </>
          )}
          <CardWrapper
            enableClick={isClickableConnections}
            isBoxFilled={isBoxFilled}
            flexDirection="row"
          >
            <CardWrapperButton
              onClick={handleClickConnections}
              enableClick={isClickableConnections}
            >
              <TSpan
                font="body-m-bold"
                colorToken={
                  isClickableConnections
                    ? '--text-tablecell-header-neutral-highlighted'
                    : '--text-tablecell-header-neutral-default'
                }
                data-testid="connected_members_count"
              >
                {connectedMembersCount}
              </TSpan>
              <TSpan
                font="footnote"
                colorToken="--text-tablecell-header-neutral-default"
              >
                {t('noumena.chamber.connections', {
                  postProcess: 'interval',
                  count: connectedMembersCount,
                })}
              </TSpan>
            </CardWrapperButton>
            {isOwner && !isMasterNoum && (
              <CardWrapperToolTipButton>
                <ToolTipHead data-title={t('noumena.noum.invite_users')}>
                  <Icon
                    name="person_add"
                    size={24}
                    onClick={() => setQuickOpenInviteModal(true)}
                  />
                </ToolTipHead>
              </CardWrapperToolTipButton>
            )}
          </CardWrapper>
          {showFollowers && (
            <CardWrapper
              enableClick={isOwner}
              onClick={() =>
                isOwner && setDefaultTab(ConnectionDetailModalTabEnum.Followers)
              }
              isBoxFilled={isBoxFilled}
            >
              <TSpan
                font="body-m-bold"
                colorToken={
                  isOwner
                    ? '--text-tablecell-header-neutral-highlighted'
                    : '--text-tablecell-header-neutral-default'
                }
              >
                {followersCount}
              </TSpan>
              <TSpan
                font="footnote"
                colorToken="--text-tablecell-header-neutral-default"
              >
                {t('noumena.chamber.followers', {
                  postProcess: 'interval',
                  count: followersCount,
                })}
              </TSpan>
            </CardWrapper>
          )}
          {showEvents && (
            <CardWrapper isBoxFilled={isBoxFilled}>
              <TSpan
                font="body-m-bold"
                colorToken="--text-tablecell-header-neutral-highlighted"
              >
                {eventMeta?.hostedEventsCount}
              </TSpan>
              <TSpan
                font="footnote"
                colorToken="--text-tablecell-header-neutral-default"
              >
                {t('noumena.noum.events', {
                  postProcess: 'interval',
                  count: eventMeta?.hostedEventsCount || 0,
                })}
              </TSpan>
            </CardWrapper>
          )}
        </MembersWrapper>
      </TopContainer>

      {isMasterNoum ? (
        <NoumsWrapper
          noumsCount={userNoumsCount}
          noums={cleanList(userNoums?.slice(0, 3)) || []}
          setDefaultTab={setDefaultTab}
          isMasterNoum={isMasterNoum}
        />
      ) : (
        <>
          {linkedNoums.length > 0 && (
            <NoumsWrapper
              noumsCount={linkedNoumsCount}
              noums={cleanList(showLinkedNoums)}
              setDefaultTab={setDefaultTab}
              isMasterNoum={isMasterNoum}
            />
          )}
          <NoumDashboardMetricsModal
            isOpen={!!dashboardType}
            dashboardType={dashboardType}
            handleClose={() => setDashboardType(undefined)}
          />
        </>
      )}

      {(isFollowing ||
        isClickableConnections ||
        linkedNoums.length > 0 ||
        (isMasterNoum &&
          defaultTab === ConnectionDetailModalTabEnum.OwnedNoums)) &&
        !connectionDetailsLoading &&
        !loadingUserNoums && (
          <ConnectionDetailsModal
            isOpen={
              !!defaultTab &&
              defaultTab !== NoumDashboardMetricsModalTabEnum.Statistics
            }
            defaultTab={defaultTab}
            handleClose={() => setDefaultTab(undefined)}
            followersCount={followersCount}
            linkedNoumsCount={linkedNoumsCount}
            connectedProjectsCount={connectedProjectsCount}
            setInvitedNoumId={setInvitedNoumId}
            showInviteModal={() => setOpenInviteModal(true)}
            userNoumsCount={userNoumsCount}
          />
        )}
      <ConnectionInviteModal
        isOpen={isOpenInviteModal}
        invitedNoumId={invitedNoumId}
        ownNoumId={space?._id || ''}
        closeInviteModal={() => setOpenInviteModal(false)}
      />
      <ChamberVisibilityInviteModal
        spaceId={space?._id || ''}
        isOpen={isQuickOpenInviteModal}
        handleClose={() => setQuickOpenInviteModal(false)}
        isOnlyInvite={true}
      />
    </MembersContainer>
  );
};

export default NoumMembers;
