import { t } from 'i18next';
import { Button } from '@/components/Button';
import { useChargebeeCheckout } from '@/features/money/hooks';
import { ChargeBeeEnums } from '../types';
import { type TPayFeeButton } from './types';

const PayFeesSetupNoum = ({
  chamberIdAfterCreatingNoum,
  publishAndSubscribeNoum,
}: TPayFeeButton) => {
  const { loading } = useChargebeeCheckout({
    success: async (subscriptionObj) => {
      if (chamberIdAfterCreatingNoum && subscriptionObj.subscription_id) {
        await publishAndSubscribeNoum(subscriptionObj.subscription_id);
      }
    },
  });

  const handleChargebeeCheckout = async () => {
    const chargbeeAnchorTag = document.getElementById(
      ChargeBeeEnums.NOUM_SETUP_USD,
    );
    chargbeeAnchorTag?.click();
  };

  return (
    <Button
      onClick={handleChargebeeCheckout}
      size="large"
      style={{ width: '193px' }}
      disabled={loading}
      secondary
    >
      {t('noumena.money.myplans.payfees')}
    </Button>
  );
};

export default PayFeesSetupNoum;
