import {
  createContext,
  type FC,
  type ReactNode,
  useCallback,
  useContext,
  useMemo,
} from 'react';
import {
  type GetNoumLinkByNoumIdQuery,
  useGetNoumLinkByNoumIdLazyQuery,
} from '@/apollo/graphql';

const initialValue = {
  noumLinkData: {} as GetNoumLinkByNoumIdQuery,
  refetch: () => {},
  getLinkData: () => {},
  loadingLinked: false,
};

export const NoumByLinkContext = createContext<{
  noumLinkData: GetNoumLinkByNoumIdQuery | undefined;
  refetch: () => void;
  loadingLinked: boolean;
  getLinkData: (spaceId: string) => void;
}>(initialValue);

export const NoumByLinkProvider: FC<{
  children: ReactNode;
}> = ({ children }) => {
  const [getData, { loading: loadingLinked, data: noumLinkData, refetch }] =
    useGetNoumLinkByNoumIdLazyQuery();

  const getLinkData = useCallback(
    (spaceId: string) => {
      getData({
        variables: {
          noumId: spaceId || '',
        },
      });
    },
    [getData],
  );

  const value = useMemo(
    () => ({
      noumLinkData,
      loadingLinked,
      refetch,
      getLinkData,
    }),
    [noumLinkData, loadingLinked, refetch, getLinkData],
  );

  return (
    <NoumByLinkContext.Provider value={value}>
      {children}
    </NoumByLinkContext.Provider>
  );
};

export const useGetNoumByLinkContext = () => useContext(NoumByLinkContext);
