import React, { useMemo } from 'react';
import { Trans, useTranslation } from 'react-i18next';

import { ConnectionRequestTypeEnum } from '@/apollo/generated/types';
import { Avatar } from '@/components/Avatar/Avatar';
import { TSpan } from '@/components/Typography';
import { getFullName } from '@/utils/fullName';
import { UserUtil } from '@/utils/user';

import { Stack } from '@/layout';
import { Button } from '@/components/Button';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { useWindowDimensions } from '@/hooks';
import { breakpoints } from '@/constants/devices';
import { type NoumEditorInvitedProps } from './types';
import {
  TextContainer,
  InvitedWrapper,
  InvitedButtonsStack,
  InvitedButtons,
} from './styles';

export const NoumEditorInvited: React.FC<NoumEditorInvitedProps> = ({
  onHandleInvitation,
  invitedFrom,
}) => {
  const { isMasterNoum } = useNoumContext();
  const { t } = useTranslation();

  const windowSize = useWindowDimensions();

  const isMobile = useMemo(
    () => windowSize.width <= breakpoints.MOBILE_L,
    [windowSize.width],
  );

  return (
    <InvitedWrapper
      fullWidth
      justify="space-between"
      align="center"
      padding="8px 16px"
      vertical={isMobile}
      gap={13}
    >
      <Stack gap={8}>
        <Avatar
          url={UserUtil.getProfilePicture(invitedFrom?.uid) ?? ''}
          size="M"
        />
        <TextContainer>
          <TSpan
            font="body-m-bold"
            colorToken="--text-card-neutral-highlighted"
          >
            <Trans
              i18nKey={
                isMasterNoum
                  ? 'noumena.editor.home.received_invite_text'
                  : 'noumena.editor.received_invite_text'
              }
              values={{
                name: getFullName(
                  invitedFrom?.uid?.firstName,
                  invitedFrom?.uid?.middleName,
                  invitedFrom?.uid?.lastName,
                ),
              }}
              components={{
                gray: (
                  <TSpan
                    data-testid="gray-text"
                    font="body-m-bold"
                    colorToken="--text-card-neutral-highlighted"
                  />
                ),
              }}
            />
          </TSpan>
        </TextContainer>
      </Stack>
      <InvitedButtonsStack gap={8}>
        <InvitedButtons
          size={isMobile ? 'full_small' : 'small'}
          onClick={() => onHandleInvitation(ConnectionRequestTypeEnum.Declined)}
        >
          {t('noumena.chamber.decline_button')}
        </InvitedButtons>
        <Button
          size="full_small"
          primary
          onClick={() => onHandleInvitation(ConnectionRequestTypeEnum.Approved)}
        >
          {t('noumena.chamber.accept_button')}
        </Button>
      </InvitedButtonsStack>
    </InvitedWrapper>
  );
};
