import React, { useCallback, useEffect, useMemo } from 'react';

import {
  type Maybe,
  ConnectionRequestTypeEnum,
  ProjectChamberType,
  SpaceTypeEnum,
} from '@/apollo/generated/types';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { useAuth } from '@/features/auth/contexts';
import { trackEvent } from '@/utils/tracking';
import { type MessagePayload } from '@/services/rest/firebase';
import {
  useRequestConnectionHelper,
  useUpdateConnectionStatusHelper,
} from '@/features/noums/hooks/spaceQuery';
import { usePushNotification } from '@/features/push-notifications/contexts/PushNotification';
import { NoumInvitedConnection } from './NoumInvitedConnection';
import { NoumUserConnection } from './NoumUserConnection';
import { NoumRequestConnection } from './NoumRequestConnection';
import { NoumUserFollow } from './NoumUserFollow';
import { NoumWaitInvitation } from './NoumWaitInvitation';
import { Wrapper } from './styles';
import { UserActionMode } from './types';
import { NoumSendMessage } from './NoumSendMessage';

export const NoumUserActions: React.FC = () => {
  const {
    spaceId,
    connectionId,
    connectionStatus,
    projectType,
    mainSpaceId,
    noumType,
    existingConnection,
    isOwner,
    onMembersInstantUpdate,
    refetchConnections,
    setConnectionStatus,
    onRefetchSpaceById,
    space,
  } = useNoumContext();

  const { onForegroundMessage } = usePushNotification();

  useEffect(
    () =>
      onForegroundMessage((message: MessagePayload) => {
        const chamberIdFromNotification = message.data?.chamberId;
        if (chamberIdFromNotification === spaceId) {
          switch (message.data?.pnId) {
            case 'connectionInviteAccepted':
              setConnectionStatus(ConnectionRequestTypeEnum.Approved);
              break;
            case 'connectionInviteDeclined':
              setConnectionStatus(ConnectionRequestTypeEnum.Declined);
              break;
          }
        }
      }),
    [onForegroundMessage, setConnectionStatus, spaceId],
  );
  const { user } = useAuth();
  const { requestConnectionHelper, loading: requestingConnectionStatus } =
    useRequestConnectionHelper();
  const { updateConnectionStatusHelper, loading: updatingConnectionStatus } =
    useUpdateConnectionStatusHelper();

  const isLoading = useMemo(
    () => requestingConnectionStatus || updatingConnectionStatus,
    [requestingConnectionStatus, updatingConnectionStatus],
  );
  /**
   * Handle connection between HomeSpace - OtherSpace(Public/Private/Secret)
   * @param nextStatus The next status that this space should be changed into.
   * @returns async boolean The result whether the api request success or failed
   *
   * @example
   *  - For direct connection request to public noums(no need owner's approval), we receive `nextStatus` as `Approved` but handle it under both case.
   *  - For connection request to private noums(need owner's approval), we receive `nextStatus` as `Requested` but handle it under both case.
   *  - For other request(Accept/Decline invitation), we use `nextStatus` directly.
   */
  const onHandleConnection = useCallback(
    async (nextStatus: ConnectionRequestTypeEnum) => {
      if (!spaceId) return false;
      let isSuccess: boolean = false;

      const prevStatus: Maybe<ConnectionRequestTypeEnum> | undefined =
        connectionStatus;

      /** Update local connection status instantly */
      setConnectionStatus(nextStatus);

      /** Update connected members as soon as the status changes */
      onMembersInstantUpdate(nextStatus);
      // Request connection directly
      if (
        [
          ConnectionRequestTypeEnum.Approved,
          ConnectionRequestTypeEnum.Requested,
        ].includes(nextStatus)
      ) {
        if (
          existingConnection &&
          existingConnection?.status &&
          ![
            String(ConnectionRequestTypeEnum.Declined),
            String(ConnectionRequestTypeEnum.Cancelled),
          ].includes(existingConnection.status)
        ) {
          isSuccess = await updateConnectionStatusHelper(
            spaceId,
            connectionId,
            nextStatus,
          );
        } else {
          // send connection request when there is no existing connection
          isSuccess = await requestConnectionHelper(mainSpaceId, spaceId);

          // In the initial connection, we need to fetch again connections
          if (isSuccess) {
            onRefetchSpaceById();
            refetchConnections();
          }
        }
      } else {
        // Handle existing connection by updating connection status
        isSuccess = await updateConnectionStatusHelper(
          spaceId,
          connectionId,
          nextStatus,
        );
      }
      /** If the request failed, revert connection status back. */
      if (!isSuccess) {
        setConnectionStatus(prevStatus);
        onMembersInstantUpdate(prevStatus);
      }
      if (
        isSuccess &&
        noumType === SpaceTypeEnum.Home &&
        nextStatus === ConnectionRequestTypeEnum.Requested
      ) {
        trackEvent('friend_request_sent', {
          UUID: user?._id,
          DeviceType: navigator.userAgent,
        });
      }

      return isSuccess;
    },
    [
      spaceId,
      connectionStatus,
      setConnectionStatus,
      onMembersInstantUpdate,
      noumType,
      existingConnection,
      updateConnectionStatusHelper,
      connectionId,
      requestConnectionHelper,
      mainSpaceId,
      onRefetchSpaceById,
      refetchConnections,
      user?._id,
    ],
  );

  /**
   * Get default user action mode when there is no connection coming.
   * @returns UserActionMode
   *
   * @example
   *  - For public noums, `Connect` button is shown
   *  - For private noums, `Request to Connect` button is shown
   */
  const getDefaultModeByType = useCallback((): UserActionMode | null => {
    let mode: UserActionMode | null = null;
    if (noumType === SpaceTypeEnum.Home) {
      mode = UserActionMode.handleRequest;
    } else if (projectType === ProjectChamberType.Public) {
      if (!isOwner && noumType === SpaceTypeEnum.Home)
        mode = UserActionMode.handleRequest;
      else mode = UserActionMode.handleConnection;
    } else if (projectType === ProjectChamberType.Private) {
      mode = UserActionMode.handleRequest;
    } else if (projectType === ProjectChamberType.Secret) {
      mode = UserActionMode.handleWaitInvitation;
    }

    return mode;
  }, [noumType, projectType, isOwner]);

  const userActionMode: UserActionMode | null = useMemo(() => {
    let mode: UserActionMode | null = null;
    if (!connectionStatus) {
      if (
        space?.connectionWithNoum?.status ===
          ConnectionRequestTypeEnum.Requested &&
        space?.connectionWithNoum?.requestTo?._id === space?._id
      )
        mode = UserActionMode.handleInvitation;
      else mode = getDefaultModeByType();
    } else if (connectionStatus === ConnectionRequestTypeEnum.Invited) {
      mode = UserActionMode.handleInvitation;
    } else if (connectionStatus === ConnectionRequestTypeEnum.Cancelled) {
      mode = getDefaultModeByType();
    } else if (connectionStatus === ConnectionRequestTypeEnum.Requested) {
      if (noumType === SpaceTypeEnum.Home) {
        const connectionWithNoum = space?.connectionWithNoum || null;
        if (
          connectionWithNoum?.status === ConnectionRequestTypeEnum.Requested
        ) {
          if (connectionWithNoum.requestFrom?._id === mainSpaceId) {
            mode = UserActionMode.handleRequest;
          } else if (connectionWithNoum.requestTo?._id === mainSpaceId) {
            mode = UserActionMode.handleInvitation;
          }
        } else mode = getDefaultModeByType();
      } else
        mode =
          projectType === ProjectChamberType.Public
            ? UserActionMode.handleConnection
            : UserActionMode.handleRequest;
    } else if (connectionStatus === ConnectionRequestTypeEnum.Approved) {
      mode = UserActionMode.handleConnection;
    } else if (
      [
        ConnectionRequestTypeEnum.Removed,
        ConnectionRequestTypeEnum.Declined,
      ].includes(connectionStatus)
    ) {
      mode = getDefaultModeByType();
    }

    return mode;
  }, [
    connectionStatus,
    getDefaultModeByType,
    mainSpaceId,
    noumType,
    projectType,
    space?._id,
    space?.connectionWithNoum,
  ]);

  return (
    <Wrapper
      data-testid="user-actions"
      enforceColumn={userActionMode === UserActionMode.handleWaitInvitation}
    >
      {/* Invited */}
      {userActionMode === UserActionMode.handleInvitation && (
        <NoumInvitedConnection
          connectionStatus={connectionStatus}
          onHandle={onHandleConnection}
        />
      )}

      {/* Request/Cancel request */}
      {userActionMode === UserActionMode.handleRequest && (
        <NoumRequestConnection
          connectionStatus={connectionStatus}
          onHandle={onHandleConnection}
          loading={isLoading}
        />
      )}
      {/* Connect/Disconnect */}
      {userActionMode === UserActionMode.handleConnection && (
        <NoumUserConnection onHandle={onHandleConnection} loading={isLoading} />
      )}
      {userActionMode === UserActionMode.handleWaitInvitation && (
        <NoumWaitInvitation />
      )}
      {noumType !== SpaceTypeEnum.Home &&
        projectType !== ProjectChamberType.Secret && <NoumUserFollow />}

      {noumType === SpaceTypeEnum.Home && space?.uid?._id ? (
        <NoumSendMessage ownerId={space.uid._id} />
      ) : null}
    </Wrapper>
  );
};
