import React, { useEffect, useMemo } from 'react';

import {
  ConnectionRequestTypeEnum,
  ProjectChamberType,
  SpaceStatusEnum,
  SpaceTypeEnum,
  UserStatus,
} from '@/apollo/generated/types';
import { StickyContainer } from '@/components/StickyContainer';
import { useAuth } from '@/features/auth/contexts';
import { useWindowDimensions } from '@/hooks';
import { sizes } from '@/constants/devices';
import { ChamberCompleteness } from '@/screens/Chamber/components/ChamberCompleteness';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { NoumByLinkProvider } from '@/screens/Chamber/components/RightPanel/NoumByLinkProvider';
import { useLaunchDarkly } from '@/hooks/launchDarkly';
import { useRequestConnectionHelper } from '@/features/noums/hooks/spaceQuery';
import { NoumEditOptions } from './elements/NoumEditOptions';
import { NoumViewModeActions } from './elements/NoumActions';
import { NoumMembers } from './elements/NoumMembers';
import ReceivedRequests from './elements/ReceivedRequests';
import InvitedByMe from './elements/InvitedByMe';
import RiseApplication from './elements/RiseApplication';
import { type RightPanelProps } from './types';
import { Container } from './styles';
import { useEditChamberState } from '../../EditChamber/provider';

const tabletMaxWidth = parseInt(sizes.TABLET_L, 10) || 1023;

export const RightPanel: React.FC<RightPanelProps> = ({
  isEditing = false,
  onSelectEditOption,
}) => {
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();
  const { isUnregistered } = useAuth();
  const { width } = useWindowDimensions();
  const {
    spaceId,
    space,
    isOwner,
    projectType,
    connectionStatus,
    isArchived,
    noumType,
    isConnected,
    mainSpaceId,
  } = useNoumContext();
  const { space: ediChamberSpace } = useEditChamberState();
  const { requestConnectionHelper } = useRequestConnectionHelper();
  const isMasterNoum = !!(space?.type === SpaceTypeEnum.Home);
  const isNoumPublishedAtAll = !(
    ediChamberSpace?.uid?.userStatus === UserStatus.Active &&
    ediChamberSpace?.status === SpaceStatusEnum.Published
  );

  const isInviteBox = useMemo(
    () =>
      (projectType === ProjectChamberType.Public ||
        projectType === ProjectChamberType.Secret) &&
      !isMasterNoum,
    [isMasterNoum, projectType],
  );
  const showCompleteness = useMemo(
    () =>
      width > tabletMaxWidth &&
      isOwner &&
      space?.uid?.userStatus === UserStatus.Active &&
      (!space?.percentCompleted || space?.percentCompleted < 100),
    [isOwner, space?.percentCompleted, space?.uid?.userStatus, width],
  );

  // Functionality for SEO enabled noums. Auto connect unregistered user to noum when user lands first time on page
  useEffect(() => {
    if (isUnregistered && !space?.isConnected && space?.enableAds && spaceId) {
      requestConnectionHelper(mainSpaceId, spaceId);
    }
  }, [
    isUnregistered,
    mainSpaceId,
    requestConnectionHelper,
    space?.enableAds,
    space?.isConnected,
    spaceId,
  ]);

  if (isUnregistered) {
    return (
      <StickyContainer>
        <Container data-testid="noum-guest-right-panel">
          <NoumByLinkProvider>
            <NoumMembers />
          </NoumByLinkProvider>
        </Container>
      </StickyContainer>
    );
  }

  if (isEditing) {
    return (
      <StickyContainer>
        <Container data-testid="noum-edit-right-panel">
          <NoumEditOptions
            onSelect={onSelectEditOption}
            isNoumPublishedAtAll={isNoumPublishedAtAll}
            projectType={ediChamberSpace?.projectType}
            enableAds={ediChamberSpace?.enableAds}
          />
        </Container>
      </StickyContainer>
    );
  }

  return (
    <StickyContainer>
      <NoumByLinkProvider>
        <Container
          noumEditor2={noumEditor2}
          data-testid="noum-view-right-panel"
        >
          <NoumViewModeActions />
          {noumType === SpaceTypeEnum.RiseApplication &&
            (isOwner || isConnected) && <RiseApplication />}
          {(isOwner ||
            isMasterNoum ||
            connectionStatus === ConnectionRequestTypeEnum.Approved) && (
            <NoumMembers />
          )}
          {showCompleteness && <ChamberCompleteness />}
          {isOwner &&
            (isInviteBox ? (
              <InvitedByMe noumId={spaceId} disabled={isArchived} />
            ) : (
              <ReceivedRequests
                noumId={spaceId}
                disabled={isArchived}
                isChambersScreen={isMasterNoum}
              />
            ))}
        </Container>
      </NoumByLinkProvider>
    </StickyContainer>
  );
};

export default RightPanel;
