import { memo } from 'react';
import generate from 'uniqid';

import {
  Modal,
  ModalBody,
  ModalFooter,
  ModalSize,
} from '@/components/ExtendedModal';
import { Spacer } from '@/layout';
import {
  type ShowOptionsModalProps,
  type NavItemType,
  type TNoumEditModal,
} from './types';
import { Buttons } from './styles';

export const NoumEditOptionsModal = memo(
  ({
    navItems,
    isOpen,
    handleClose,
    handleOptionSelection,
    isNoumPublishedAtAll,
  }: ShowOptionsModalProps) => {
    const handleOptionSelected = (value: TNoumEditModal) => {
      handleOptionSelection(value);
      handleClose();
    };

    return (
      <Modal
        closeButtonStyles={{ enforceLeft: true }}
        testId="showOptions"
        open={isOpen}
        onClose={handleClose}
        enableCloseButton
        size={ModalSize.S}
        disableBackdropClick
      >
        <Spacer height={56} />
        <ModalBody>
          {navItems.map(
            ({
              label,
              type,
              value,
              show,
              disableBeforeFirstPublish,
            }: NavItemType) =>
              show ? (
                <Buttons
                  key={generate()}
                  testId={`option-${value}`}
                  size="large"
                  {...(type === 'error'
                    ? { secondary: true, intent: 'negative' }
                    : { tertiary: true })}
                  onClick={() => handleOptionSelected(value)}
                  disabled={isNoumPublishedAtAll && disableBeforeFirstPublish}
                >
                  {label}
                </Buttons>
              ) : undefined,
          )}
        </ModalBody>
        <ModalFooter />
      </Modal>
    );
  },
);
