import {
  type Dispatch,
  type FC,
  type SetStateAction,
  useCallback,
} from 'react';
import { Trans } from 'react-i18next';
import { t } from 'i18next';
import { Spacer } from '@/layout';
import BasicChipsTabsForm from '@/components/Tabs/TabsForm';
import { useWindowDimensions } from '@/hooks';
import { breakpoints } from '@/constants/devices';
import {
  type CustomPreviewTabEnum,
  customPreviewRightPanelList,
} from './constants';
import { CustomPreviewHeader, StyledTSpan } from './styles';

type CustomPreviewRightPanelProps = {
  selectedCustomPreviewTab: CustomPreviewTabEnum;
  setSelectedCPreviewTab:
    | Dispatch<SetStateAction<CustomPreviewTabEnum>>
    | undefined;
};

export const CustomPreviewRightPanel: FC<CustomPreviewRightPanelProps> = ({
  selectedCustomPreviewTab,
  setSelectedCPreviewTab,
}) => {
  const onTabChange = useCallback(
    (value: CustomPreviewTabEnum) => {
      setSelectedCPreviewTab?.(value);
    },
    [setSelectedCPreviewTab],
  );

  const { width } = useWindowDimensions();
  const isMobile = width < breakpoints.MOBILE_L;

  return (
    <CustomPreviewHeader>
      <div>
        <BasicChipsTabsForm
          onChange={onTabChange}
          inputList={customPreviewRightPanelList}
          selectedId={selectedCustomPreviewTab}
          mode="isBackground"
          isWithoutImage
          fontSize="--font-link-medium-size"
          tabWidth={isMobile ? '140px' : '165px'}
          textFont="--font-body-medium-regular-font"
        />
        <Spacer height={16} />
        <Trans
          i18nKey={t('noumena.noum_edit.custom_previews.descriptiontop')}
          components={{
            span: <StyledTSpan />,
          }}
        />
      </div>
    </CustomPreviewHeader>
  );
};
