import { type FC } from 'react';

import { RightPanel } from '@/screens/Chamber/components/RightPanel';
import { CustomPreviewRightPanel } from '@/screens/Chamber/CustomPreview/CustomPreviewRightpanel';
import { useLaunchDarkly } from '@/hooks';
import { HeadDCPTab, Header, MainLayout, Root } from './styles';
import { type LayoutProps } from './types';

export const ChamberViewLayout: FC<LayoutProps> = ({
  header,
  isEditing,
  onSelectEditOption,
  hasSideBar = true,
  children,
  leftSidebar,
  isCustomPreview = false,
  hasThemePanel = false,
  selectedCustomPreviewTab,
  setSelectedCPreviewTab,
}) => {
  const { flags } = useLaunchDarkly();

  return (
    <Root>
      <MainLayout
        data-testid="Main-Layout"
        centralize={!isEditing && flags.newAppNavigation}
        hasThemePanel={hasThemePanel}
      >
        {flags.newAppNavigation ? null : <div id="space">{leftSidebar}</div>}
        <Header topSpacing={!!leftSidebar} id="header">
          {header}
        </Header>
        <div id="body">{children}</div>
        <div id="sidebar">
          {hasSideBar && (
            <RightPanel
              isEditing={isEditing}
              onSelectEditOption={onSelectEditOption}
            />
          )}
          {isCustomPreview && selectedCustomPreviewTab && (
            <HeadDCPTab>
              <CustomPreviewRightPanel
                selectedCustomPreviewTab={selectedCustomPreviewTab}
                setSelectedCPreviewTab={setSelectedCPreviewTab}
              />
            </HeadDCPTab>
          )}
        </div>
      </MainLayout>
    </Root>
  );
};
