import { useCallback } from 'react';
import { t } from 'i18next';
import * as Sentry from '@sentry/react';
import { differenceInDays } from 'date-fns';
import { useLaunchDarkly } from '@/hooks/launchDarkly';
import { useToast } from '@/hooks/toast';
import { useGetNoumTransactionFeeDetailsLazyQuery } from '@/apollo/graphql';
import { SpaceTypeEnum } from '@/apollo/generated/types';

export function useCheckChamberCanBeUnArchived() {
  const { addToast } = useToast();
  const { flags } = useLaunchDarkly();

  const handleError = useCallback(
    (networkError: String | Error | null) => {
      addToast(
        'error',
        'none',
        `${t('noumena.toast_error.text')}: ${networkError}`,
      );
    },
    [addToast],
  );

  const [gqlGetNoumTransactionFeeDetails] =
    useGetNoumTransactionFeeDetailsLazyQuery();

  const checkDateIsGraeterThanToday = useCallback(
    (inputDate: string | null | undefined) => {
      let res = false;
      const now = new Date();
      if (inputDate) {
        const diff = differenceInDays(new Date(inputDate), now);
        if (diff > 0) {
          res = true;
        }
      }
      return res;
    },
    [],
  );

  const checkChamberCanBeUnArchived = useCallback(
    async (
      chamberId: string | undefined | null,
      toggleModal: (open: boolean) => void,
      setSetUpSlotForNoum: (open: boolean) => void,
      spaceType: string | undefined | null,
    ) => {
      if (
        flags.paymentSubscriptions &&
        spaceType === SpaceTypeEnum.Project &&
        chamberId
      ) {
        await gqlGetNoumTransactionFeeDetails({
          fetchPolicy: 'network-only',
          variables: { noumDetailInput: { chamber_id: chamberId } },
          onError: ({ networkError = null, graphQLErrors = [] }) => {
            const [err] = graphQLErrors;
            handleError(err?.message ?? networkError);
            Sentry.captureException(new Error(err?.message ?? networkError), {
              tags: {
                section: 'gqlGetNoumTransactionFeeDetails',
              },
            });
          },
          onCompleted: (response) => {
            if (
              checkDateIsGraeterThanToday(
                response?.getNoumTransactionFeeDetails[0]?.valid_till,
              )
            ) {
              toggleModal(true);
            } else {
              setSetUpSlotForNoum(true);
            }
          },
        });
      } else {
        toggleModal(true);
      }
    },
    [
      checkDateIsGraeterThanToday,
      flags.paymentSubscriptions,
      gqlGetNoumTransactionFeeDetails,
      handleError,
    ],
  );

  return { checkChamberCanBeUnArchived };
}

export default useCheckChamberCanBeUnArchived;
