import { t } from 'i18next';
import {
  useState,
  memo,
  type Dispatch,
  type SetStateAction,
  useEffect,
} from 'react';
import { Checkbox } from '@/components/Checkbox';
import { Icon } from '@/components/Icon';
import { TSpan } from '@/components/Typography';
import { useAuth } from '@/features/auth/contexts';
import { useToast, useLaunchDarkly } from '@/hooks';
import { useGenerateTokenForCQ } from '@/features/money/hooks';
import { Modal, ModalSize } from '@/components/ExtendedModal';
import { Spacer, Stack } from '@/layout';
import { Tag } from '@/components/Tag';
import EVENTS from '@/constants/trackingEvents';
import { trackEvent } from '@/utils/tracking';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import {
  StepWrapper,
  StepContent,
  RiseIFrameContainer,
  RiseIframe,
} from './styles';
import { StepName } from './types';

interface CQStepProps {
  step: string;
  showCheckbox: boolean;
  url: string | null;
  noumId: string | undefined;
  applicationId: string | undefined;
  stepCompleted: boolean;
  onModalClosed: Function;
  checked: boolean;
  noOfEssays: number;
  isClassDeleted: boolean;
  canEdit: boolean;
  identityStepCompleted: boolean;
  essayQuestionAnswered: () => boolean;
  onCheckBoxClicked: Dispatch<
    SetStateAction<{
      principlesYou: boolean;
      essays: boolean;
    }>
  >;
}

const CQStep = memo(
  ({
    step,
    applicationId,
    stepCompleted,
    showCheckbox,
    url,
    noumId,
    onModalClosed,
    onCheckBoxClicked,
    checked,
    noOfEssays,
    isClassDeleted,
    canEdit,
    essayQuestionAnswered,
    identityStepCompleted,
  }: CQStepProps) => {
    const generateTokenForCQ = useGenerateTokenForCQ();
    const { addToast } = useToast();
    const { user } = useAuth();
    const { flags } = useLaunchDarkly();
    const [modal, setModal] = useState<{
      open: boolean;
      source: string;
    }>({
      open: false,
      source: '',
    });
    const { isOwner } = useNoumContext();

    const onStepClick = async () => {
      if (step === StepName.PRINCIPLES_YOU && !flags.principlesYou) {
        window.open(url!, '_blank');
        trackEvent(EVENTS.RISE.PRINCIPLES_YOU_CLICKED, {
          UUID: user?._id,
        });
        return;
      }

      if (step === StepName.FINANCIAL && !identityStepCompleted) {
        addToast(
          'error',
          'icon',
          t('noumena.rise_program.identity.capital.error'),
        );
        return;
      }

      const { token, error } = await generateTokenForCQ();
      if (token) {
        const source = `${url}?access_token=${token}&rise_application=true&applicationId=${applicationId}&noumId=${noumId}`;
        // window.open(source, '_blank');
        setModal({ open: true, source });
        return;
      }
      if (error) {
        addToast('error', 'none', error.message);
      }
    };

    const onCloseClick = () => {
      onModalClosed();
      setModal({ open: false, source: '' });
    };

    const handleEssay = (check: boolean) => {
      if (!checked) {
        const res = essayQuestionAnswered();
        if (res) {
          onCheckBoxClicked((previous) => ({ ...previous, essays: false }));
          addToast(
            'error',
            'icon',
            t('noumena.rise_program.esssay_not_answered'),
          );
        } else {
          onCheckBoxClicked((previous) => ({ ...previous, essays: check }));
          trackEvent(EVENTS.RISE.APPLICATION_STEP, {
            UUID: user?._id,
            title: 'Essays',
          });
        }
      } else {
        onCheckBoxClicked((previous) => ({ ...previous, essays: check }));
      }
    };

    const handleCheck = (check: boolean) => {
      if (isClassDeleted) {
        addToast('error', 'icon', t(`noumena.rise_program.can_user_apply`));
        return;
      }
      if (!canEdit) {
        addToast('error', 'icon', t('noumena.rise_program.not.owner.error'));
      }
      switch (step) {
        case StepName.ESSAYS:
          handleEssay(check);
          break;
        case StepName.PRINCIPLES_YOU:
          onCheckBoxClicked((previous) => ({
            ...previous,
            principlesYou: check,
          }));
          break;
      }
    };

    const isStepEssay = step === StepName.ESSAYS;
    const isStepPrinciplesYou = step === StepName.PRINCIPLES_YOU;

    useEffect(() => {
      if (stepCompleted && !isStepEssay && !isStepPrinciplesYou) {
        trackEvent(EVENTS.RISE.APPLICATION_STEP, {
          UUID: user?._id,
          title: t(step),
        });
      }
    }, [step, stepCompleted, user, isStepEssay, isStepPrinciplesYou]);

    return (
      <>
        <StepWrapper allowCursor={isOwner}>
          <StepContent
            onClick={() => {
              if (!url) return;
              if (isClassDeleted) {
                addToast(
                  'error',
                  'icon',
                  t(`noumena.rise_program.can_user_apply`),
                );
                return;
              }
              if (!canEdit) {
                addToast(
                  'error',
                  'icon',
                  t('noumena.rise_program.not.owner.error'),
                );
              }
              onStepClick();
            }}
            style={!isOwner ? { pointerEvents: 'none' } : {}}
          >
            <TSpan
              font="body-m"
              colorToken="--text-tablecell-header-neutral-highlighted"
            >
              {isStepEssay ? t(step, { noOfEssays }) : t(step)}
            </TSpan>
            {stepCompleted ? (
              <Tag secondary success>
                Done
              </Tag>
            ) : (
              isStepEssay || (
                <Icon
                  name="chevron_right_m"
                  size={12}
                  color={
                    showCheckbox
                      ? '--icon-tablecell-neutral-default'
                      : '--icon-tablecell-neutral-highlighted'
                  }
                  style={{ marginRight: '9.15px' }}
                />
              )
            )}
          </StepContent>
          {showCheckbox && (
            <>
              <Spacer height={9} />
              <Stack gap={8} justify="flex-start">
                <Checkbox
                  isChecked={checked}
                  onChange={handleCheck}
                  disableClick={!isOwner}
                  icon={
                    <Icon
                      name="tick_m"
                      size={23.5}
                      color="--icon-checkbox-neutral-alt-default"
                    />
                  }
                />
                <TSpan
                  font="body-m"
                  colorToken="--text-tablecell-header-neutral-default"
                >
                  I confirm that I completed this step
                </TSpan>
              </Stack>
            </>
          )}
        </StepWrapper>
        <Modal
          open={modal.open}
          size={ModalSize.XXL}
          enableCloseButton
          onClose={onCloseClick}
        >
          <RiseIFrameContainer>
            <RiseIframe src={modal.source} title="rise" />
          </RiseIFrameContainer>
        </Modal>
      </>
    );
  },
);

export default CQStep;
