import { type FC, useCallback, useState, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import generate from 'uniqid';

import { Icon } from '@/components/Icon';
import { TSpan } from '@/components/Typography';
import { NoumEditOptionsModal } from '@/screens/Chamber/components/modals/NoumEditOptionsModal';
import { useLaunchDarkly } from '@/hooks/launchDarkly';

import { ProjectChamberType } from '@/apollo/generated/types';
import { type NoumEditOptionsProps, type TNoumEditModal } from './types';
import { NavigationItems } from './constants';
import {
  OptionsContainer,
  OptionWrapper,
  ShowOptionsWrapper,
  ShowOptionsButton,
  SubWrapper,
} from './styles';

export const NoumEditOptions: FC<NoumEditOptionsProps> = ({
  onSelect,
  isNoumPublishedAtAll,
  projectType,
  enableAds,
}) => {
  const { t } = useTranslation();
  const { flags } = useLaunchDarkly();
  const [showOptions, setShowOptions] = useState<boolean>(false);

  const onItemClick = useCallback(
    (value: TNoumEditModal) => onSelect?.(value),
    [onSelect],
  );

  const showNavigationItems = useMemo(
    () =>
      NavigationItems.map((item) => ({
        ...item,
        show:
          item.value === 'manage_members'
            ? flags.elementPermission && flags.noumEditor2
            : item.value === 'broadcasting'
            ? flags.broadcast
            : item.value === 'custom_preview'
            ? flags.customNoums
            : item.value === 'noum_ads'
            ? flags.noumAds && projectType === ProjectChamberType.Public
            : item.value === 'noumena_copilot'
            ? false
            : ['save_as_a_template', 'restore_last_published_version'].includes(
                item.value,
              )
            ? flags.noumEditor2
            : true,
      })),
    [flags, projectType],
  );

  return (
    <>
      <OptionsContainer data-testid="sideBar">
        {showNavigationItems.map(
          ({ label, type, value, show, disableBeforeFirstPublish }) =>
            show ? (
              <OptionWrapper
                key={generate()}
                onClick={() => {
                  if (!(disableBeforeFirstPublish && isNoumPublishedAtAll)) {
                    onItemClick(value);
                  }
                }}
                data-testid="navItem"
                disabled={disableBeforeFirstPublish && isNoumPublishedAtAll}
              >
                <TSpan
                  data-testid={`noum-edit-option-${label}`}
                  font="body-m-bold"
                  colorToken={
                    disableBeforeFirstPublish && isNoumPublishedAtAll
                      ? '--text-button-neutral-disabled'
                      : type === 'error'
                      ? '--text-tablecell-header-danger-primary-highlighted'
                      : '--text-tablecell-header-neutral-highlighted'
                  }
                >
                  {label}
                </TSpan>
                <SubWrapper>
                  {value === 'noum_ads' && !enableAds && (
                    <TSpan
                      font="footnote"
                      colorToken="--text-tablecell-body-neutral-default"
                    >
                      Off
                    </TSpan>
                  )}
                  <Icon
                    name="chevron_small_right_m"
                    size={24}
                    {...(disableBeforeFirstPublish && isNoumPublishedAtAll
                      ? {
                          color: '--text-button-neutral-disabled',
                        }
                      : type === 'error'
                      ? {
                          color: '--icon-tablecell-danger-primary-default',
                        }
                      : {
                          color: '--icon-tablecell-neutral-highlighted',
                        })}
                  />
                </SubWrapper>
              </OptionWrapper>
            ) : undefined,
        )}
      </OptionsContainer>
      <ShowOptionsWrapper data-testid="showoptions">
        <ShowOptionsButton
          size="large"
          secondary
          testId="showoptionsBtn"
          onClick={() => setShowOptions(true)}
        >
          {t('noumena.chamber.show_options')}
        </ShowOptionsButton>
      </ShowOptionsWrapper>
      <NoumEditOptionsModal
        navItems={showNavigationItems}
        isOpen={showOptions}
        handleOptionSelection={onItemClick}
        handleClose={() => setShowOptions(false)}
        isNoumPublishedAtAll={isNoumPublishedAtAll}
      />
    </>
  );
};

export default NoumEditOptions;
