import { memo, useCallback } from 'react';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { ApplicationResultStatus } from '@/apollo/generated/types';
import { useGetNoumClassByNoumIdQuery } from '@/apollo/graphql';
import { useRiseQuestions } from '@/features/noums/hooks/spaceQuery';
import SubmitMyApplication from './SubmitMyApplication';
import StatusScreen from './StatusScreen';
import SkeletonLoader from './SkeletonLoader';

export const RiseApplication = memo(() => {
  const { spaceId, isOwner, isConnected } = useNoumContext();
  const {
    applicationId,
    resultJson,
    refetch,
    status,
    loading,
    questions,
    parentNoumId,
  } = useRiseQuestions(spaceId!);

  const { data, loading: noumClassLoading } = useGetNoumClassByNoumIdQuery({
    skip: !parentNoumId,
    variables: {
      noumId: parentNoumId!,
    },
  });

  const isClassDeleted = data?.getNoumClassByNoumId?.isDeleted ?? false;

  const essayQuestionAnswered = useCallback(
    () =>
      (questions || []).some((essayKey) => {
        const answer = String(resultJson?.[essayKey?.id as keyof JSON] || '');
        const cleanAnswer =
          (answer && answer?.replace(/(<([^>]+)>)/gi, '')) || '';
        return cleanAnswer.length < 200;
      }),
    [questions, resultJson],
  );

  if (loading || noumClassLoading) {
    return <SkeletonLoader />;
  }

  if (!status) {
    return null;
  }

  return (
    <>
      {status === ApplicationResultStatus.Inprogress || isConnected ? (
        <>
          <StatusScreen status={status} />
          <SubmitMyApplication
            applicationId={applicationId}
            resultJSON={resultJson}
            refetch={refetch}
            spaceId={spaceId}
            noOfEssays={questions?.length ?? 0}
            isClassDeleted={isClassDeleted}
            canEdit={isOwner}
            essayQuestionAnswered={essayQuestionAnswered}
          />
        </>
      ) : (
        <StatusScreen status={status} />
      )}
    </>
  );
});
