import { t } from 'i18next';
import { useCallback, useEffect, useState } from 'react';
import Skeleton from 'react-loading-skeleton';
import { useNavigate } from 'react-router';
import { TSpan } from '@/components/Typography';
import * as ModalComponent from '@/components/ExtendedModal';
import { Spacer } from '@/layout';
import {
  WrapperLoading,
  WrapperSpinner,
} from '@/screens/Chamber/components/ElementWrapper/styles';
import { CircleProgressBar } from '@/components/ProgressBar';
import { Button } from '@/components/Button';
import { useAuth } from '@/features/auth/contexts';
import {
  useSubscription,
  useLinkNoumToPlanHelper,
} from '@/features/money/hooks';
import { trackEvent } from '@/utils/tracking';
import { SpaceTypeEnum } from '@/apollo/generated/types';
import { Spinner } from '@/components/Spinner/Spinner';
import { useNoumDetails } from '@/features/noums/hooks/noums';
import { usePublishSpaceHelper } from '@/features/noums/hooks/spaceQuery';
import { DateAfterOneYear, DateFormatter } from '../helper';
import { PurschaseModalLaunchFronEnum } from '../types';
import PayFeesSetupNoum from './PayFeesSetupNoum';
import PlanPurchaseModal from '../PlanPurchaseModal';
import {
  PlanInfo,
  PlanMetaData,
  PlanDescription,
  PlanRenewalBox,
} from './styles';
import { type SetupNoumModalProps } from './types';

const SetupNoumModal = ({
  open,
  onClose,
  chamberIdAfterCreatingNoum,
}: SetupNoumModalProps) => {
  const [planModalIsOpen, setPlanModdalIsOpen] = useState<boolean>(false);
  const [existingPlan, setExistingPlan] = useState<boolean>(false);
  const [loadOldPayAsYouGo, setLoadOldPayAsYouGo] = useState<boolean>(false);
  const [subscriptionId, setSubscriptionId] = useState<number | undefined>();
  const [waiting, setWaiting] = useState(false);
  const { user, masterId: mainSpaceId } = useAuth();
  const { space } = useNoumDetails(chamberIdAfterCreatingNoum!);

  const {
    totalNoumSetupSlots,
    activeNoumSetupSlots,
    oldestPlanNoumSetup,
    loading,
    payAsYouGoNoumSetup,
  } = useSubscription();
  const navigate = useNavigate();
  const { linkNoumToPlanHelper } = useLinkNoumToPlanHelper();
  const { onRefetchSpaceByConfig, onRefetchSpaceById } = useNoumDetails(
    mainSpaceId || '',
  );

  const { publishSpaceHelper, loading: subscribing } = usePublishSpaceHelper();

  useEffect(() => {
    if (payAsYouGoNoumSetup.length > 0) {
      setExistingPlan(true);
      setLoadOldPayAsYouGo(true);
      setSubscriptionId(
        payAsYouGoNoumSetup[payAsYouGoNoumSetup.length - 1]?.subscription_id!,
      );
    } else if (totalNoumSetupSlots - activeNoumSetupSlots > 0) {
      setExistingPlan(true);
      setSubscriptionId(oldestPlanNoumSetup[0]?.subscription_id!);
    } else {
      setExistingPlan(false);
    }
  }, [
    activeNoumSetupSlots,
    oldestPlanNoumSetup,
    payAsYouGoNoumSetup,
    totalNoumSetupSlots,
  ]);

  const onAddNewPlan = useCallback(async () => {
    setPlanModdalIsOpen(true);
  }, []);

  const onClosePurshaseModal = useCallback(() => {
    setPlanModdalIsOpen(false);
  }, []);

  const closeSetupNoumModal = () => {
    onClose();
  };

  const newFunction = useCallback(
    async (subId?: number) => {
      await linkNoumToPlanHelper(chamberIdAfterCreatingNoum!, subId);
      const isSuccess = await publishSpaceHelper(
        chamberIdAfterCreatingNoum!,
        space,
      );
      setWaiting(false);
      if (space?.type === SpaceTypeEnum.Project) {
        trackEvent('publishPN', {
          UUID: user?._id,
          ProjectNoumID: chamberIdAfterCreatingNoum!,
        });
      }
      if (isSuccess) {
        onRefetchSpaceByConfig();
        onRefetchSpaceById();
        navigate(`/noum/${chamberIdAfterCreatingNoum}`, { replace: true });
      }
      onClose();
    },
    [
      chamberIdAfterCreatingNoum,
      navigate,
      linkNoumToPlanHelper,
      onClose,
      onRefetchSpaceByConfig,
      onRefetchSpaceById,
      publishSpaceHelper,
      space,
      user?._id,
    ],
  );

  const publishAndSubscribeNoum = useCallback(
    (subId?: number) => {
      if (!subId) return;
      setWaiting(true);
      setTimeout(() => newFunction(subId), 1000);
    },
    [newFunction],
  );

  // const setupNoumSlots = useCallback(async () => {
  //   await linkNoumToPlanHelper(
  //     chamberIdAfterCreatingNoum!,
  //     subscriptionId,
  //     togglePublishModal,
  //   );
  //   onClose();
  // }, [
  //   chamberIdAfterCreatingNoum,
  //   linkNoumToPlanHelper,
  //   onClose,
  //   subscriptionId,
  //   togglePublishModal,
  // ]);

  return (
    <>
      <ModalComponent.Modal
        open={open || subscribing}
        size={ModalComponent.ModalSize.M}
        enableCloseButton={!(waiting || subscribing)}
        onClose={closeSetupNoumModal}
        disableBackdropClick
      >
        {waiting || subscribing ? (
          <WrapperLoading>
            <WrapperSpinner>
              <Spinner />
              <Spacer height="20px" />
            </WrapperSpinner>
            <Spacer height="16px" />
            <TSpan
              data-testid="bodyChamberPublishSaving"
              font="body-l"
              colorToken="--text-modal-neutral-default"
            >
              {t(`noumena.container.chamber_publish.body.loading`)}
            </TSpan>
          </WrapperLoading>
        ) : loading ? (
          <div style={{ width: '100%' }}>
            <Skeleton count={4} borderRadius={16} height={75} />
          </div>
        ) : (
          <>
            <ModalComponent.ModalHeader>
              {existingPlan
                ? t('noumena.money.myplans.setupnoum')
                : t('noumena.money.myplans.payasyougo')}
            </ModalComponent.ModalHeader>

            <ModalComponent.ModalBody align="center" hideScrollbar>
              {existingPlan ? (
                <>
                  <TSpan
                    font="body-l"
                    colorToken="--text-modal-neutral-default"
                  >
                    {loadOldPayAsYouGo
                      ? t('noumena.money.myplans.setupnoum.pay_as_you_bodytext')
                      : t('noumena.money.myplans.setupnoum.bodytext')}
                  </TSpan>
                  <Spacer height={16} />
                  <PlanInfo>
                    <TSpan
                      font="body-l-bold"
                      colorToken="--text-tablecell-header-neutral-highlighted"
                    >
                      {loadOldPayAsYouGo
                        ? 'Pay As You Go'
                        : oldestPlanNoumSetup[0]?.item_price_id?.split('-')[0]}
                    </TSpan>
                    <PlanMetaData>
                      {!loadOldPayAsYouGo && (
                        <PlanDescription>
                          <TSpan
                            font="footnote"
                            colorToken="--text-card-neutral-default"
                          >
                            {t('noumena.money.myplans.expire')}
                          </TSpan>
                          <TSpan
                            font="body-m"
                            colorToken="--text-card-neutral-highlighted"
                          >
                            {DateFormatter(oldestPlanNoumSetup[0]?.valid_till)}
                          </TSpan>
                        </PlanDescription>
                      )}
                      <PlanRenewalBox>
                        <CircleProgressBar
                          percentage={
                            loadOldPayAsYouGo
                              ? (0 / 1) * 100
                              : ((oldestPlanNoumSetup[0]
                                  ?.active_count_noum_setup
                                  ? oldestPlanNoumSetup[0]
                                      .active_count_noum_setup
                                  : 0) /
                                  (oldestPlanNoumSetup[0]?.max_count_noum_setup
                                    ? oldestPlanNoumSetup[0]
                                        .max_count_noum_setup
                                    : 0)) *
                                100
                          }
                          color={
                            ((oldestPlanNoumSetup[0]?.active_count_noum_setup
                              ? oldestPlanNoumSetup[0].active_count_noum_setup
                              : 0) /
                              (oldestPlanNoumSetup[0]?.max_count_noum_setup
                                ? oldestPlanNoumSetup[0].max_count_noum_setup
                                : 0)) *
                              100 ===
                            0
                              ? 'var(--bg-progressbar-neutral-default)'
                              : 'var(--bg-progressbar-brand-primary-default)'
                          }
                          barSize={3}
                          circleSize={24}
                        />
                        <PlanDescription>
                          <TSpan
                            font="footnote"
                            colorToken="--text-card-neutral-default"
                          >
                            {t('noumena.money.myplans.noumsetup')}
                          </TSpan>
                          <TSpan
                            font="body-m"
                            colorToken="--text-card-neutral-highlighted"
                          >
                            {loadOldPayAsYouGo
                              ? `0 / 1`
                              : `${oldestPlanNoumSetup[0]?.active_count_noum_setup} /
                        ${oldestPlanNoumSetup[0]?.max_count_noum_setup}`}
                          </TSpan>
                        </PlanDescription>
                      </PlanRenewalBox>
                    </PlanMetaData>
                  </PlanInfo>
                  <Spacer height={16} />
                  <TSpan
                    font="footnote"
                    colorToken="--text-input-neutral-default"
                  >
                    {t('noumena.money.myplans.setupnoum.expirytext')}
                  </TSpan>
                  <TSpan
                    font="body-m-bold"
                    colorToken="--text-input-neutral-filled"
                  >
                    {DateAfterOneYear()}
                  </TSpan>
                </>
              ) : (
                <TSpan
                  font="body-l"
                  colorToken="--text-modal-neutral-default"
                  textAlign="center"
                  style={{ width: '370px' }}
                >
                  {t('noumena.money.myplans.payasyougo.modaltext')}
                </TSpan>
              )}
            </ModalComponent.ModalBody>
            <ModalComponent.ModalFooter gap={16}>
              {existingPlan ? null : (
                <PayFeesSetupNoum
                  chamberIdAfterCreatingNoum={chamberIdAfterCreatingNoum}
                  publishAndSubscribeNoum={publishAndSubscribeNoum}
                />
              )}
              {existingPlan ? (
                <Button
                  primary
                  size="full"
                  onClick={() => publishAndSubscribeNoum(subscriptionId)}
                >
                  Setup Now
                </Button>
              ) : (
                <Button
                  primary
                  size="large"
                  style={{ width: '193px' }}
                  onClick={() => {
                    onAddNewPlan();
                  }}
                >
                  {t('noumena.money.myplans.addNewPlan')}
                </Button>
              )}
            </ModalComponent.ModalFooter>
          </>
        )}
      </ModalComponent.Modal>
      {planModalIsOpen && (
        <PlanPurchaseModal
          open={planModalIsOpen}
          onClose={onClosePurshaseModal}
          launchFrom={PurschaseModalLaunchFronEnum.NOUM_SETUP}
          chamberIdAfterCreatingNoum={chamberIdAfterCreatingNoum}
          publishAndSubscribeNoum={publishAndSubscribeNoum}
        />
      )}
    </>
  );
};

export default SetupNoumModal;
