import { type DateRange } from 'react-day-picker';
import { t } from 'i18next';
import { useMemo } from 'react';
import { getBottomStatusFromQuery, Infinite } from '@/components/Infinite';
import { Stack } from '@/layout';
import { Spinner } from '@/components/Spinner';
import { type NoumDashboardMetricsModalTabEnum } from '../types';
import { useNoumDashboardListsHelper } from '../useNoumDashboardListsHelper';
import { TextOnlySpan } from '../styles';
import { ListItemContent } from './ListItemContent';
import { SpinnerContainer } from './styles';

const ListsTab = ({
  selectedTab,
  dateRange,
  noumId,
}: {
  selectedTab: NoumDashboardMetricsModalTabEnum;
  dateRange: DateRange;
  noumId: string;
}) => {
  const { currentData, loading, totalCount, networkStatus, fetchMore } =
    useNoumDashboardListsHelper(noumId, selectedTab, dateRange);

  const listsComponent = useMemo(() => {
    if (loading) {
      return null;
    }
    if (currentData && currentData.length > 0) {
      return (
        <>
          {currentData.map((item) => (
            <ListItemContent
              key={item?._id}
              item={item}
              selectedTab={selectedTab}
              gap={16}
            />
          ))}
        </>
      );
    }
    return (
      <Stack fullWidth justify="center">
        <TextOnlySpan
          font="body-l"
          colorToken="--text-card-neutral-highlighted"
        >
          {t('noumena.chamber.link.no_value', { value: selectedTab })}
        </TextOnlySpan>
      </Stack>
    );
  }, [loading, currentData, selectedTab]);

  return loading ? (
    <SpinnerContainer>
      <Spinner />
    </SpinnerContainer>
  ) : (
    <Infinite
      onFetchMore={fetchMore}
      status={getBottomStatusFromQuery({
        networkStatus,
        totalCount,
        currentCount: currentData?.length || 0,
      })}
      disableFetchMoreWhileLoading={true}
      isSpinnerRelative
      paddingRight={currentData && currentData.length > 0 ? '12px' : '0'}
      style={{
        overflowX: 'hidden',
      }}
      width="100%"
    >
      {listsComponent}
    </Infinite>
  );
};
export default ListsTab;
