import { t } from 'i18next';
import { memo, useState } from 'react';
import { type Maybe } from 'graphql/jsutils/Maybe';
import { useAuth } from '@/features/auth/contexts';
import { useError, useToast, useLaunchDarkly } from '@/hooks';
import { Button } from '@/components/Button';
import { TSpan } from '@/components/Typography';
import { ModalBody, Modal, ModalHeader } from '@/components/ExtendedModal';
import { Spacer, Stack } from '@/layout';
import { Spinner } from '@/components/Spinner/Spinner';
import { useSubmitRiseApplicationMutation } from '@/apollo/graphql';
import {
  ApplicationResultStatus,
  SpaceTypeEnum,
} from '@/apollo/generated/types';
import EVENTS from '@/constants/trackingEvents';
import { trackEvent } from '@/utils/tracking';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { type ResultJSON, StepName } from './types';
import CQ_STEPS, { cqPrinciplesYou } from './helper';
import CQStep from './CQStep';
import { Wrapper, ApplicationInformation, CQStepWrapper } from './styles';
import { RiseApplicationReviewButton } from '../NoumActions/RiseApplicationReviewButton';

interface Props {
  applicationId: string | undefined;
  resultJSON: Maybe<JSON> | undefined;
  spaceId: Maybe<string> | undefined;
  refetch: () => void;
  noOfEssays: number;
  canEdit: boolean;
  isClassDeleted: boolean;
  essayQuestionAnswered: () => boolean;
}

const SubmitMyApplication = memo(
  ({
    applicationId,
    spaceId,
    resultJSON,
    refetch,
    noOfEssays,
    canEdit,
    isClassDeleted,
    essayQuestionAnswered,
  }: Props) => {
    const { logError } = useError();
    const { addToast } = useToast();
    const { user } = useAuth();
    const { isOwner, isConnected, space } = useNoumContext();

    const { flags } = useLaunchDarkly();

    const [submitApplication, { loading: submitApplicationLoading }] =
      useSubmitRiseApplicationMutation({
        onCompleted: () => {
          trackEvent(EVENTS.RISE.APPLICATION_SUBMIT, {
            UUID: user?._id,
          });
          refetch();
        },
        onError: (error) => {
          logError(error, 'submit-rise-application', true);
        },
      });
    const [canSubmit, setCanSubmit] = useState(false);
    const [checked, setChecked] = useState<{
      principlesYou: boolean;
      essays: boolean;
    }>({
      principlesYou: false,
      essays: false,
    });

    const {
      businessFinancialProjectionStatus,
      BusinessPlanStatus,
      identityStatus,
      nativeStatus,
      financialCreditStatus,
      financialCashflowStatus,
      principleYouStatus,
    } = (resultJSON || {}) as ResultJSON;

    const identityCompletion = identityStatus === 'SUBMITTED';
    const nativeCompletion = nativeStatus === 'SUBMITTED';
    const financialCompletion =
      financialCashflowStatus === 'SUBMITTED' &&
      financialCreditStatus === 'SUBMITTED';
    const businessCompletion =
      businessFinancialProjectionStatus === 'SUBMITTED' &&
      BusinessPlanStatus === 'SUBMITTED';

    const riseFormCompletion =
      identityCompletion &&
      nativeCompletion &&
      financialCompletion &&
      businessCompletion;

    const principlesYouCompletion = principleYouStatus === 'SUBMITTED';

    const enableApplicationSubmission = flags.principlesYou
      ? riseFormCompletion && principlesYouCompletion && checked.essays
      : riseFormCompletion && checked.principlesYou && checked.essays;

    const getStepCompletionStatus = (step: StepName): boolean => {
      switch (step) {
        case StepName.IDENTITY:
          return identityCompletion;
        case StepName.NATIVE:
          return nativeCompletion;
        case StepName.FINANCIAL:
          return financialCompletion;
        case StepName.BUSINESS:
          return businessCompletion;
        case StepName.PRINCIPLES_YOU:
          return flags.principlesYou ? principlesYouCompletion : false;
        default:
          return false;
      }
    };

    const handleSubmitModal = () => {
      if (isClassDeleted) {
        addToast('error', 'icon', t(`noumena.rise_program.can_user_apply`));
        return;
      }
      const res = essayQuestionAnswered();
      if (res) {
        addToast(
          'error',
          'icon',
          t('noumena.rise_program.esssay_not_answered'),
        );
      } else {
        setCanSubmit(!canSubmit);
      }
    };

    const handleSubmitButtonClick = () => {
      setCanSubmit(false);
      if (applicationId) {
        submitApplication({
          variables: {
            _id: applicationId,
            input: {
              resultJSON,
              status: ApplicationResultStatus.Submitted,
            },
          },
        });
      }
    };

    return (
      <Wrapper>
        {space?.type === SpaceTypeEnum.RiseApplication && isConnected && (
          <RiseApplicationReviewButton refreshStatus={refetch} />
        )}
        {isOwner && (
          <Button
            size="full"
            onClick={handleSubmitModal}
            primary
            disabled={!enableApplicationSubmission}
          >
            {t('noumena.chamber.rise.submit_my_application')}
          </Button>
        )}
        {isOwner && (
          <ApplicationInformation>
            <div>
              <TSpan font="body-m" colorToken="--text-infobox-neutral-default">
                {t('noumena.chamber.rise.submit_my_application_info')}
              </TSpan>
            </div>
          </ApplicationInformation>
        )}
        <CQStepWrapper>
          {CQ_STEPS.map((cqStep) => {
            const { step, url } = cqStep;
            const isCompleted = getStepCompletionStatus(step);
            const newURL =
              step === StepName.PRINCIPLES_YOU && flags.principlesYou
                ? cqPrinciplesYou
                : url;

            return (
              <CQStep
                key={step}
                step={step}
                showCheckbox={
                  (step === StepName.PRINCIPLES_YOU && !flags.principlesYou) ||
                  step === StepName.ESSAYS
                }
                url={newURL}
                noumId={spaceId || ''}
                applicationId={applicationId}
                stepCompleted={isCompleted}
                onModalClosed={refetch}
                onCheckBoxClicked={setChecked}
                noOfEssays={noOfEssays}
                isClassDeleted={isClassDeleted}
                canEdit={canEdit}
                identityStepCompleted={identityCompletion}
                checked={
                  step === StepName.PRINCIPLES_YOU
                    ? checked.principlesYou
                    : checked.essays
                }
                essayQuestionAnswered={essayQuestionAnswered}
              />
            );
          })}
        </CQStepWrapper>
        <Modal open={canSubmit} disableBackdropClick disableEscapeKeyDown>
          <ModalHeader>
            <TSpan font="heading-s-bold">
              {t('noumena.chamber.rise.submit_my_application')}
            </TSpan>
          </ModalHeader>
          <ModalBody align="center" style={{ width: '279px' }}>
            <TSpan
              font="body-l"
              textAlign="center"
              colorToken="--text-modal-neutral-default"
            >
              {t('noumena.chamber.rise.submit_my_application_modal_info')}
            </TSpan>
            <Spacer height={24} />
            <Button onClick={handleSubmitButtonClick} primary size="full">
              {t('noumena.submit')}
            </Button>
            <Spacer height={16} />
            <Button tertiary size="full" onClick={handleSubmitModal}>
              {t('noumena.cancel')}
            </Button>
          </ModalBody>
        </Modal>{' '}
        <Modal
          disableBackdropClick
          disableEscapeKeyDown
          open={submitApplicationLoading}
          style={{ width: '327px' }}
        >
          <Stack align="center" justify="center" vertical>
            <Spacer height={24} />
            <div style={{ position: 'relative' }}>
              <Spinner />
            </div>
            <Spacer height={24} />
            <TSpan
              font="body-l"
              colorToken="--text-body-neutral-default"
              textAlign="center"
            >
              {t('noumena.chamber.rise.submitting_application')}
            </TSpan>
          </Stack>
        </Modal>
      </Wrapper>
    );
  },
);

export default SubmitMyApplication;
