import { useCallback } from 'react';
import { t } from 'i18next';
import * as Sentry from '@sentry/react';
import { useAddRenewNoumTransactionFeeMutation } from '@/apollo/graphql';
import { Noum_Fee_Operation_Type } from '@/apollo/generated/types';
import { useToast } from '@/hooks/toast';
import { useLaunchDarkly } from '@/hooks/launchDarkly';

export function useLinkNoumToPlanHelper() {
  const { addToast } = useToast();

  const handleError = useCallback(
    (networkError: String | Error | null) => {
      addToast(
        'error',
        'none',
        `${t('noumena.toast_error.text')}: ${networkError}`,
      );
    },
    [addToast],
  );

  const handleSuccess = useCallback(() => {
    addToast('success', 'icon', `'Noum Linked To Plan.`);
  }, [addToast]);

  const [addRenewNoumMutation] = useAddRenewNoumTransactionFeeMutation();
  const { flags } = useLaunchDarkly();

  const linkNoumToPlanHelper = useCallback(
    async (
      chamberId: string | null,
      noumSubscriptionId: number | null | undefined,
      togglePublishModal?: Function | undefined,
    ) => {
      if (chamberId && noumSubscriptionId && flags.paymentSubscriptions) {
        await addRenewNoumMutation({
          variables: {
            noumInput: {
              chamber_id: chamberId,
              operation_type: Noum_Fee_Operation_Type.Activation,
              subscription_id: noumSubscriptionId,
            },
          },
          onError: ({ networkError = null, graphQLErrors = [] }) => {
            const [err] = graphQLErrors;
            handleError(err?.message ?? networkError);
            Sentry.captureException(new Error(err?.message ?? networkError), {
              tags: {
                section: 'addRenewNoumMutation',
              },
            });
          },
          onCompleted: (res) => {
            if (res.addRenewNoumTransactionFee.noum_transaction_fee_id) {
              handleSuccess();
              togglePublishModal?.(true);
            }
          },
        });
      }
    },
    [
      addRenewNoumMutation,
      flags.paymentSubscriptions,
      handleError,
      handleSuccess,
    ],
  );

  return { linkNoumToPlanHelper };
}

export default useLinkNoumToPlanHelper;
