import { type LinkedNoumFragment } from '@/apollo/graphql';
import { Avatar } from '@/components/Avatar/Avatar';
import { Button } from '@/components/Button';
import { TSpan } from '@/components/Typography';
import { Spacer } from '@/layout';
import { ConnectionDetailModalTabEnum } from '@/screens/Chamber/components/modals/ConnectionDetailsModal/types';
import { t } from 'i18next';
import { memo, type FC } from 'react';
import { Trans } from 'react-i18next';
import {
  AvatarWrapper,
  LinkedBottom,
  LinkedItem,
  LinkedWrapper,
  SeeAllWrapper,
} from './styles';

export interface NoumsWrapperProps {
  isMasterNoum?: boolean;
  noums: LinkedNoumFragment[];
  noumsCount: number;
  setDefaultTab: (tabName: string) => void;
}

export const NoumsWrapper: FC<NoumsWrapperProps> = memo(
  ({ isMasterNoum, noumsCount = 0, noums, setDefaultTab }) => (
    <>
      <LinkedWrapper>
        <TSpan font="footnote-bold" colorToken="--text-body-neutral-default">
          <Trans
            i18nKey={
              isMasterNoum
                ? 'noumena.home_noum.owned_noums'
                : 'noumena.chamber.linked_noums'
            }
            values={{ count: noumsCount }}
            tOptions={{
              postProcess: 'interval',
              count: noumsCount,
            }}
            components={{
              b: (
                <TSpan
                  font="footnote-bold"
                  colorToken="--text-body-neutral-highlighted"
                />
              ),
            }}
          />
        </TSpan>
        <Spacer height={16} />
        {noums.map((noum) => (
          <LinkedItem key={noum?._id}>
            <AvatarWrapper>
              <Avatar url={noum?.profileImage} size="M" />
            </AvatarWrapper>
            <TSpan
              font="body-m-bold"
              colorToken="--text-tablecell-header-neutral-highlighted"
            >
              {noum?.name}
            </TSpan>
          </LinkedItem>
        ))}
        <LinkedBottom />
      </LinkedWrapper>
      <SeeAllWrapper>
        <Button
          textOnly
          onClick={() =>
            setDefaultTab(
              isMasterNoum
                ? ConnectionDetailModalTabEnum.OwnedNoums
                : ConnectionDetailModalTabEnum.Noums,
            )
          }
        >
          {isMasterNoum
            ? t('noumena.home_noum.owned_noums.see_all')
            : t('noumena.chamber.see_all_linked_noums')}
        </Button>
      </SeeAllWrapper>
    </>
  ),
);
