import { type FC } from 'react';
import Skeleton from 'react-loading-skeleton';

import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { Spacer } from '@/layout';
import { SpaceTypeEnum } from '@/apollo/generated/types';
import { NoumOwnerActions } from './NoumOwnerActions';
import { NoumUserActions } from './NoumUserActions';
import { HomeNoumActions } from './HomeNoumActions';
import { ActionsWrapper } from './styles';
import { RiseNoumUserApplyButton } from './RiseNoumUserApplyButton';

export const NoumViewModeActions: FC = () => {
  const { isOwner, isMasterNoum, loadingSpace, space, isConnected } =
    useNoumContext();

  if (loadingSpace) {
    return (
      <ActionsWrapper data-testid="view-mode-actions-skeleton">
        <Skeleton height={56} borderRadius={8} />
      </ActionsWrapper>
    );
  }
  if (isOwner && space?.type === SpaceTypeEnum.RiseApplication) return null;
  return (
    <ActionsWrapper>
      {/*  space.category rise apply now button */}
      {space?.category?.name === 'Rise' && isConnected && (
        <RiseNoumUserApplyButton />
      )}
      <Spacer height={12} />
      {isOwner ? (
        isMasterNoum ? (
          <HomeNoumActions />
        ) : (
          <NoumOwnerActions />
        )
      ) : (
        <NoumUserActions />
      )}
    </ActionsWrapper>
  );
};

export default NoumViewModeActions;
