import {
  type FC,
  memo,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import { useTranslation } from 'react-i18next';
import { Modal, ModalHeader, ModalSize } from '@/components/ExtendedModal';
import BasicChipsTabsForm from '@/components/Tabs/TabsForm';
import { type InputListTypes } from '@/components/Tabs/types';
import { type NoumLink, ProjectChamberType } from '@/apollo/generated/types';
import { getBottomStatusFromQuery, Infinite } from '@/components/Infinite';
import { useNoumContext } from '@/screens/Chamber/ViewChamber/ChamberProvider';
import { DeviceTypeEnum, useDeviceType, useWindowDimensions } from '@/hooks';
import { breakpoints } from '@/constants/devices';
import {
  ConnectionDetailModalTabEnum,
  type ConnectionDetailsModalProps,
} from './types';
import {
  TabSectionHead,
  TextOnlySpan,
  Container,
  NoResultsContainer,
  CustomModalBody,
} from './styles';
import { useConnectionDetailsHelper } from './useConnectionDetailsHelper';
import { ConnectionDetailsContent } from './ConnectionDetailsContent';
import { getModalHeadList } from './ModalHeadList';

export const ConnectionDetailsModal: FC<ConnectionDetailsModalProps> = memo(
  ({
    isOpen,
    defaultTab,
    handleClose,
    followersCount,
    linkedNoumsCount,
    setInvitedNoumId,
    showInviteModal,
    connectedProjectsCount = 0,
    userNoumsCount = 0,
  }) => {
    const { width } = useWindowDimensions();
    const isTablet = width < breakpoints.LAPTOP;

    const { t } = useTranslation();
    const [selectedId, setSelectedId] = useState('0');
    const [selectedTab, setSelectedTab] = useState(defaultTab || '');

    const {
      isMasterNoum,
      isOwner,
      isArchived,
      isConnected,
      link,
      connectedMembers,
      connectedMembersCount,
      spaceId,
      projectType,
      space,
    } = useNoumContext();

    const deviceType = useDeviceType();
    const isSecretNoum = projectType === ProjectChamberType.Secret;

    const isMobile = useMemo(
      () => deviceType === DeviceTypeEnum.MOBILE,
      [deviceType],
    );

    const enableConnectionsAndFollowers = useMemo(
      () => isOwner && !isMasterNoum && !isSecretNoum,
      [isMasterNoum, isOwner, isSecretNoum],
    );

    const enableConnectionsOnly = useMemo(
      () => isConnected || isMasterNoum || isSecretNoum,
      [isConnected, isMasterNoum, isSecretNoum],
    );

    const isHomeNoumConnectionDetail = useMemo(
      () =>
        isMasterNoum && defaultTab !== ConnectionDetailModalTabEnum.OwnedNoums,
      [defaultTab, isMasterNoum],
    );

    const { currentData, loading, totalCount, networkStatus, fetchMore } =
      useConnectionDetailsHelper(
        spaceId || '',
        selectedTab,
        link?._id,
        isMasterNoum ? space?.uid?._id : null,
      );

    const modalHeadList: InputListTypes[] = useMemo(
      () =>
        getModalHeadList({
          isHomeNoumConnectionDetail,
          connectedMembersCount,
          linkedNoumsCount,
          isConnected,
          isOwner,
          isMasterNoum,
          followersCount,
          isSecretNoum,
          connectedProjectsCount,
          defaultTab,
          userNoumsCount,
        }),
      [
        isHomeNoumConnectionDetail,
        connectedMembersCount,
        linkedNoumsCount,
        isConnected,
        isOwner,
        isMasterNoum,
        followersCount,
        isSecretNoum,
        connectedProjectsCount,
        defaultTab,
        userNoumsCount,
      ],
    );

    const { refetchConnectedMembers } = useNoumContext();
    useEffect(() => {
      if (isOpen === true) {
        refetchConnectedMembers();
      }
    }, [isOpen, refetchConnectedMembers]);

    const title = useMemo(() => {
      if (modalHeadList.length > 1) {
        return linkedNoumsCount
          ? t('noumena.noum_link.link_details')
          : enableConnectionsAndFollowers
          ? t('noumena.noum.connections_and_followers')
          : enableConnectionsOnly
          ? isHomeNoumConnectionDetail
            ? t(`noumena.noum.home.connections`, {
                connectionsCount:
                  connectedMembersCount + connectedProjectsCount,
              })
            : t('noumena.noum.connections')
          : t('noumena.noum.followers');
      }
      return modalHeadList[0]?.text || '';
    }, [
      modalHeadList,
      linkedNoumsCount,
      t,
      enableConnectionsAndFollowers,
      enableConnectionsOnly,
      isHomeNoumConnectionDetail,
      connectedMembersCount,
      connectedProjectsCount,
    ]);

    const handleChange = useCallback(
      (id: string) => {
        if (id) {
          setSelectedId(id);
          setSelectedTab(modalHeadList[parseInt(id, 10)].name);
        }
      },
      [modalHeadList],
    );

    const tabComponent = useMemo(() => {
      if (loading) {
        return null;
      }
      if (currentData && currentData.length > 0) {
        return (
          <>
            {(currentData as NoumLink['linkedNoums']).map((item) => (
              <ConnectionDetailsContent
                key={item?._id}
                item={item}
                selectedTab={selectedTab}
                connectedMembers={connectedMembers}
                isOwner={isOwner}
                isArchived={isArchived}
                closeModal={handleClose}
                setInvitedNoumId={setInvitedNoumId}
                showInviteModal={showInviteModal}
                gap={isHomeNoumConnectionDetail ? 16 : 12}
              />
            ))}
          </>
        );
      }
      return (
        <NoResultsContainer>
          <TextOnlySpan
            font="body-l"
            colorToken="--text-card-neutral-highlighted"
          >
            {t('noumena.chamber.link.no_value', { value: selectedTab })}
          </TextOnlySpan>
        </NoResultsContainer>
      );
    }, [
      t,
      loading,
      currentData,
      selectedTab,
      connectedMembers,
      isOwner,
      isArchived,
      isHomeNoumConnectionDetail,
      handleClose,
      setInvitedNoumId,
      showInviteModal,
    ]);

    useEffect(() => {
      if (defaultTab) {
        const defaultId = modalHeadList.findIndex(
          (item) => item.name === defaultTab,
        );
        if (defaultId !== -1) {
          setSelectedId(`${defaultId}`);
          setSelectedTab(defaultTab);
        }
      } else {
        setSelectedTab('');
      }
    }, [defaultTab, modalHeadList]);

    return (
      <Modal
        testId="testRequestsAndInvites"
        open={isOpen}
        onClose={handleClose}
        enableCloseButton
        size={ModalSize.L}
        isFullScreen={isMobile}
        closeButtonStyles={{
          enforceLeft: isTablet,
        }}
        disableBackdropClick
      >
        <Container data-testid="connection_details_container">
          <ModalHeader>{title}</ModalHeader>
          {modalHeadList.length > 1 && (
            <TabSectionHead autoWidth={isHomeNoumConnectionDetail}>
              <BasicChipsTabsForm
                onChange={handleChange}
                inputList={modalHeadList}
                selectedId={selectedId}
                mode="isBackground"
                isWithoutImage
                fontSize="--font-button-small-size"
                fullWidth={true}
                textFont="--font-body-medium-regular-font"
              />
            </TabSectionHead>
          )}
          <CustomModalBody
            style={{ padding: 0 }}
            customHeight={modalHeadList.length <= 1}
          >
            <Infinite
              onFetchMore={fetchMore}
              status={getBottomStatusFromQuery({
                networkStatus,
                totalCount,
                currentCount: currentData?.length || 0,
              })}
              disableFetchMoreWhileLoading={true}
              isSpinnerRelative
              paddingRight={
                currentData && currentData.length > 0 ? '12px' : '0'
              }
              style={{
                overflowX: 'hidden',
              }}
              width="100%"
            >
              {tabComponent}
            </Infinite>
          </CustomModalBody>
        </Container>
      </Modal>
    );
  },
);
