import { useLaunchDarkly } from '@/hooks/launchDarkly';
import { type LayoutProps } from './types';
import { NoumViewLayout } from './NoumViewLayout';
import { ChamberViewLayout } from './ChamberViewLayout';

export default (props: LayoutProps) => {
  const {
    flags: { noumEditor2 },
  } = useLaunchDarkly();

  return noumEditor2 && !props.isCustomPreview ? (
    <NoumViewLayout {...props} />
  ) : (
    <ChamberViewLayout {...props} />
  );
};
